html,
body {
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden;
    background-color: #ffffff;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-y: auto;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.hero {
    height: 100%;
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/static/media/hero_image_placeholder.716ab2af.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
}

.heroTitle {
    color: white;
    font-family: 'Audhistine';
    font-size: 3.7vw;
}

.heroText {
    text-align: center;
    width: 100%;
    position: absolute;
    top: 65%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.heroSubtitle {
    color: white;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 2vw;
    letter-spacing: .2vw;
}

p.avoidwrap {
    display: inline-block;
}

@media only screen and (max-width: 700px) {
    .heroTitle {
        font-size: 8vw;
    }
    .heroSubtitle {
        font-size: 4vw;
    }
}

.btn {
    width: 90px;
    height: 45px;
    cursor: pointer;
    background: rgba(255, 255, 255, 0.199);
    border: 0px solid white;
    outline: none;
    transition: 1s ease-in-out;
    position: relative;
    text-align: center;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
}

svg {
    position: absolute;
    left: 0px;
    top: 0px;
    fill: none;
    stroke: #fff;
    stroke-dasharray: 150 480;
    stroke-dashoffset: 150;
    transition: 1s ease-in-out;
}

.btn:hover {
    transition: 1s ease-in-out;
    background: rgba(0, 0, 0, 0.103);
}

.btn:hover svg {
    stroke-dashoffset: -270;
}

.btn p {
    color: white;
    font-size: 18px;
    font-family: 'Josefin Sans', sans-serif;
    text-align: center;
}

@font-face {
    font-family: 'Audhistine';
    src: local('Audhistine'), url(/static/media/Audhistine.c78d5b69.ttf) format('truetype');
}

p {
    font-family: 'Josefin Sans', sans-serif;
    font-size: 1.25em;
    /* letter-spacing: .2vw; */
}

h1 {
